import _m0 from "protobufjs/minimal";
import { BenchmarkName } from "./benchmark_name";
import { Timestamp } from "../../google/protobuf/timestamp";

export const protobufPackage = "snapchat.cdp.cof";

export interface BenchmarkValue {
  intValue: number | undefined;
  longValue: number | undefined;
  boolValue: boolean | undefined;
  floatValue: number | undefined;
}

export interface BenchmarkRequest {
  /**
   * Benchmarks a client is requested to execute. They are ordered by priority.
   * Benchmark in the head of the list has the highest priority.
   */
  benchmarkNames: BenchmarkName[];
  /**
   * Client can ignore benchmarks that have not been executed after this timestamp. The client should still try to
   * report the results from benchmarks that have already been executed.
   */
  expirationTimestamp: Date | undefined;
}

export interface BenchmarkResult {
  name: BenchmarkName;
  value: BenchmarkValue | undefined;
}

function createBaseBenchmarkValue(): BenchmarkValue {
  return { intValue: undefined, longValue: undefined, boolValue: undefined, floatValue: undefined };
}

