export const protobufPackage = "snapchat.lenses";

/** denotes user's bitmoji related data. */
export interface BitmojiUserInfo {
  /** unique avatar id for a user's bitmoji */
  avatarId: string;
  /** selfie id for a user's bitmoji */
  selfieId: string;
}

/** denotes information related to friendship if userdata is representing a friend. */
export interface FriendUserInfo {
  /** time when friendship started , represented in time(seconds) since epoch */
  friendshipStart: Date | undefined;
  /** optional time when last interacted , represented in time(seconds) since epoch */
  lastInteraction: Date | undefined;
  /** optional streak count with current user. */
  streak: number;
}

/** model representing all user related info */
export interface UserData {
  /** unique user id for a user */
  userId: string;
  /** user name for a user */
  username: string;
  /** optional birthdate of the user if its shared represented in %d-%d-%d(year-month-day). */
  birthdate: string;
  /** optional display name of the user if its shared. */
  displayName: string;
  /** optional country code of the user if its shared. */
  countrycode: string;
  /** snap score of the user if its available. */
  score: number;
  /** optional bitmoji related info if user is a bitmoji user. */
  bitmojiInfo: BitmojiUserInfo | undefined;
  /** optional friendship related info if user is a friend user. */
  friendInfo: FriendUserInfo | undefined;
  /** optional zodiac of a user */
  zodiac: UserData_Zodiac;
}

export enum UserData_Zodiac {
  Invalid = "Invalid",
  Aquarius = "Aquarius",
  Aries = "Aries",
  Cancer = "Cancer",
  Capricorn = "Capricorn",
  Gemini = "Gemini",
  Leo = "Leo",
  Libra = "Libra",
  Pisces = "Pisces",
  Sagittarius = "Sagittarius",
  Scorpio = "Scorpio",
  Taurus = "Taurus",
  Virgo = "Virgo",
  UNRECOGNIZED = "UNRECOGNIZED",
}

export function userData_ZodiacFromJSON(object: any): UserData_Zodiac {
  switch (object) {
    case 0:
    case "Invalid":
      return UserData_Zodiac.Invalid;
    case 1:
    case "Aquarius":
      return UserData_Zodiac.Aquarius;
    case 2:
    case "Aries":
      return UserData_Zodiac.Aries;
    case 3:
    case "Cancer":
      return UserData_Zodiac.Cancer;
    case 4:
    case "Capricorn":
      return UserData_Zodiac.Capricorn;
    case 5:
    case "Gemini":
      return UserData_Zodiac.Gemini;
    case 6:
    case "Leo":
      return UserData_Zodiac.Leo;
    case 7:
    case "Libra":
      return UserData_Zodiac.Libra;
    case 8:
    case "Pisces":
      return UserData_Zodiac.Pisces;
    case 9:
    case "Sagittarius":
      return UserData_Zodiac.Sagittarius;
    case 10:
    case "Scorpio":
      return UserData_Zodiac.Scorpio;
    case 11:
    case "Taurus":
      return UserData_Zodiac.Taurus;
    case 12:
    case "Virgo":
      return UserData_Zodiac.Virgo;
    case -1:
    case "UNRECOGNIZED":
    default:
      return UserData_Zodiac.UNRECOGNIZED;
  }
}

export function userData_ZodiacToNumber(object: UserData_Zodiac): number {
  switch (object) {
    case UserData_Zodiac.Invalid:
      return 0;
    case UserData_Zodiac.Aquarius:
      return 1;
    case UserData_Zodiac.Aries:
      return 2;
    case UserData_Zodiac.Cancer:
      return 3;
    case UserData_Zodiac.Capricorn:
      return 4;
    case UserData_Zodiac.Gemini:
      return 5;
    case UserData_Zodiac.Leo:
      return 6;
    case UserData_Zodiac.Libra:
      return 7;
    case UserData_Zodiac.Pisces:
      return 8;
    case UserData_Zodiac.Sagittarius:
      return 9;
    case UserData_Zodiac.Scorpio:
      return 10;
    case UserData_Zodiac.Taurus:
      return 11;
    case UserData_Zodiac.Virgo:
      return 12;
    default:
      return 0;
  }
}

/** model representing list of users to help wrapping in api */
export interface UserDataList {
  userData: UserData[];
}

