import _m0 from "protobufjs/minimal";

export const protobufPackage = "snapchat.cdp.cof";

/** Steps for AB process. */
export enum AbStep {
  USER_WHITELISTED = 0,
  USER_GROUP_WHITELISTED = 1,
  FILTER_CONDITIONS = 2,
  SLICE_RANGE = 3,
  TRAFFIC_ALLOCATED = 4,
  UNRECOGNIZED = -1,
}

/** Steps for COF process. */
export enum CofStep {
  VALIDATE_REQUEST = 0,
  REQUEST_ATLAS = 1,
  BUILD_TARGETING_INFO = 2,
  QUERY_INDEX = 3,
  REQUEST_AB = 4,
  UNRECOGNIZED = -1,
}

export interface AbStepInfo {
  abStep: AbStep;
  info: string;
}

export interface CofStepInfo {
  cofStep: CofStep;
  info: string;
}

export interface DebugTrace {
  /** The last reached COF step if the requested config is not returned. */
  lastCofStep: CofStepInfo | undefined;
  /** The last reached AB step if the requested config is not returned from AB. */
  lastAbStep: AbStepInfo | undefined;
  /**
   * Budget group ID that the user is assigned to.
   * This field will be populated if a Budget A/B is retrieved and the user has a classical BGID.
   * Some users fall into superposition BGID and do not have classical BGID.
   */
  budgetGroupId: number;
}

